import {useState, useEffect} from 'react';
import {BrowserRouter as Router, Route, Routes} from 'react-router-dom';
import {Auth, Home} from './pages';
// import Navbar from "./tweets/components/Navbar"
import {apiGetCurrentProfile} from './api/profiles'



function App() {
  const [currUser, setCurrUser] = useState();
  const [didFetchUserProfile, setDidFetchUserProfile] = useState(false);

  function profileFetchCallback(response, status) {
    console.log("App.js>>> ", response);
    if (status === 200) {
      setCurrUser(response);
    }
  }

  useEffect(() => {
    let mounted = true;
    const fetchProfile = async () => {
      if (!mounted || didFetchUserProfile) {return}
      apiGetCurrentProfile(profileFetchCallback);
      setDidFetchUserProfile(true);
    }

    fetchProfile()
    return () => {
      mounted = false;
    }
  }, [didFetchUserProfile]);


  

  return (
    <div className="App">
      <Router>
        <Routes>
         
            <Route exact path='/' element={<Home currUser={currUser} />}/>  
        </Routes>
      </Router>
    </div>
  );
}

export default App;
