import { useState, useEffect } from 'react';
import { apiTweetAction, apiGetTweetData } from '../tweet_functions'
import { apiGetProfileData } from '../../api/profiles'
import { UserLink, UserPicture, UserDisplay } from '../../profiles'
import TweetOptionsBtn from './TweetOptionsBtn'

import '../styles/Tweet.css'

 
function Tweet({ tweet, className, currentUser, nextUrl }) { 
    console.log(tweet)
    const [didLike, setDidLike] = useState(false);
    const [didRetweet, setDidRetweet] = useState(false);
    const [likes, setLikes] = useState(tweet ? tweet.likes : 0);
    const [isDeleted, setIsDeleted] = useState(false);
    const [profile, setProfile] = useState();
    const [didLookupProfile, setDidLookupProfile] = useState(false);
    const [didLookupTweet, setDidLookupTweet] = useState(false);


    const handleLink = (e) => {
        e.preventDefault()
        e.stopPropagation()
        window.location.href = `/${tweet.id}`
    }    
    

    const handleBackendActionEvent = (response, status) => {
        if (status === 403) {
            window.location.href = "/login/"
        } else if (status !== 404) {
            apiGetTweetData(handleTweetDataCallback, tweet.id)
        }
    }


    function handleTweetAction(action, event) {
        event.stopPropagation()
        event.preventDefault()
        

        let currentAction = action
        
        if (action === 'like') {
            if (didLike) {
                currentAction = 'unlike'
            }
        } else if (action === 'retweet' & didRetweet) {
            return
        }
        apiTweetAction(tweet.id, currentAction, handleBackendActionEvent)
    }

    const Retweet = ({ callback }) => {
        return tweet.parent ? (
            <div onClick={((e) => handleLink(e))}>
                {console.log(tweet.user)}
                <p className="retweet-header">retweeted by <UserLink user={tweet.user}>{tweet.user.username}</UserLink></p>
                <Tweet tweet={tweet.parent} className={tweet.content ? "retweet" : "retweet no-border"} currentUser={currentUser} />
            </div>
        ) : null
    }


    const handleTweetDataCallback = (response, status) => {
        if (status === 200) {
            setDidRetweet(response['didRetweet']);
            setDidLike(response['didLike']);
            setLikes(response['likes']);
            setDidLookupTweet(true);

            if (response['likes'] === 0) {
                setDidLike(false)
            }

        } else {
            console.log(response, status)
        }
    }

    useEffect(() => {
        let isMounted = true;
        const fetchData = async () => {
            if (!isMounted) {return}
            if (!didLookupTweet) {
                apiGetTweetData(handleTweetDataCallback, tweet.id);
            }
        }
        fetchData();
        return () => {
            isMounted = false;
        }
    }, [didLookupTweet, tweet]); 
    
    let willRender = (!isDeleted && tweet && (tweet.content || tweet.parent))


    return willRender ? (
        <div className={className + " overall-container"} onClick={((e) => handleLink(e))}>
            {!tweet.parent &&
            <div className="profile-container d-flex">
                <div className="mx-1 profile-picture">
                    {tweet.user ? <UserLink><UserPicture user={tweet.user}/></UserLink>: "Loading..."}
                </div>
                <p className="tweet-header">
                    <UserDisplay user={tweet.user} /> 
                </p>
                <TweetOptionsBtn tweet={tweet} currentUser={currentUser} setIsDeleted={setIsDeleted}/>
            </div>}
            <div className="tweet-container">
                {tweet.content !== "" &&
                <div className="tweet-content-wrapper">
                    <p className="tweet-text">{`${tweet.content}`}</p>
                </div>
                }
                
                {!tweet.parent ?
                    <div className="buttons">
                        <i className={didRetweet ? `fas fa-retweet btn-icon rt-icon text-green` : "fas fa-retweet btn-icon rt-icon"} onClick={((e) => handleTweetAction("retweet", e))}></i>
                        <div className="like-btn__container">
                            <i className={didLike ? "fas fa-heart btn-icon like-icon red-text" : "far fa-heart btn-icon like-icon"} onClick={((e) => handleTweetAction("like", e))}></i>
                            {likes}
                        </div>
                    </div>
                : 
                <div className="retweet-container">
                    <Retweet/>
                </div>}
            </div>
        </div>
    ) : <p>hello</p>
}

export default Tweet;


