import { useEffect, useState } from 'react'
import Tweet from './Tweet'
import { apiTweetFeed } from '../tweet_functions'
import '../styles/TweetFeed.css'


const TweetFeedList = ({ newTweets, setNewTweets, tweets, setTweets, username, tweetsDidSet, callback, nextUrl, handleLoadNextResponse }) => {
  const [loading, setLoading] = useState(true);

  const handleLoadNext = (e) => {
    e.preventDefault();
    if (nextUrl !== null) {
      apiTweetFeed(username, handleLoadNextResponse, nextUrl);
    }
  }

  useEffect(() => {
    if (newTweets.length > 0) {
      setTweets([...newTweets, ...tweets]);
      setNewTweets([]);
    }
    // eslint-disable-next-line
  }, [newTweets]);
  
  useEffect(() => {
    let isMounted = true
    const fetchTweetFeed = async () => {
      if (!isMounted) {return}
      if (!tweetsDidSet) {
        apiTweetFeed(username, callback);
      }
    }
    fetchTweetFeed();
    setLoading(false);
    return () => {
      isMounted = false;
    }
    
  }, [tweetsDidSet, username, callback]);


  return (!loading && tweets.length > 0) ? (
      <div className="tweet_feed__container">
          {tweets.map((tweet, idx) => {return <Tweet tweet={tweet} idx={idx} key={`${idx}-{tweet.id}`} callback={callback} currentUser={username} nextUrl={nextUrl}/>})}
          {nextUrl && <button className='btn btn-outline btn-primary' onClick={handleLoadNext}>Load More</button>}
      </div>
  ) : 
    <div className="tweet_feed__container">
      <p>Loading...</p>;
    </div>
}

export default TweetFeedList;