import React, {useRef} from 'react';
import {UserImages} from '../../profiles';
import {apiEditProfile} from '../../api/profiles';

import './styles.css';
import banner from '../../media/forest_banner.jpg';

function ProfileEdit(props) {
    const {user, closeModal, callback} = props
    const usernameRef = useRef(user.username);
    const bioRef = useRef(user.bio);
    const locationRef = useRef(user.location);

   



    const saveProfile = (event) => {
        event.preventDefault();
        if (dataHasChanged()) {
            const data = {
                "username": usernameRef.current.value,
                "bio": bioRef.current.value,
                "location": locationRef.current.value
            }
            apiEditProfile(callback, data);

        }
        closeModal(event);
    }

    const dataHasChanged = () => {
        console.log(usernameRef.current.value, user.username);
        if (usernameRef.current.value !== user.username) {
            return true
        }
        if (bioRef.current.value !== user.bio) {
            return true
        }
        if (locationRef.current.value !== user.location) {
            return true
        }
        // const data = [usernameRef.current, bioRef.current, locationRef.current]
        // const userData = [user.username, user.bio, user.location]
        // data.map((data, i) => {
        //     console.log(data, i);
        //     if (data !== userData[i]) {
        //         return true
        //     }
        // });
        return false;
    };

    return (
        <div className="modal-container">
            <div className="overlay"></div>
            <div className="edit-profile__container">
                <div className="edit__banner">
                    <div className="close-modal__container">
                        <span className="close-modal__bg" onClick={closeModal}><i class="fas fa-times close-modal"></i></span>
                    </div>
                    <div className="banner__text__container">
                        <p className="banner__text">Edit Profile</p>
                    </div>
                    <button className="save-btn" onClick={saveProfile}>Save</button>
                </div>
                <UserImages user={user} banner={banner}/>
                <div className="profile-forms">
                    <form>
                        <div className="form-container">
                            <label>Username</label>
                            <textarea className="edit username" ref={usernameRef}>{usernameRef.current}</textarea>
                        </div>
                        <div className="form-container">
                            <label>Bio</label>
                            <textarea className="edit bio" ref={bioRef}>{bioRef.current}</textarea>
                        </div>
                        <div className="form-container small-form">
                            <label>Location</label>
                            <textarea className="edit location" ref={locationRef}>{locationRef.current}</textarea>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    );
}

export default ProfileEdit;
