import {useEffect, useState} from 'react';
import { apiTweetDetail } from '../../tweets/tweet_functions';
import Tweet from '../../tweets/components/Tweet'

function TweetDetail({currUser}) {
    const [didLookupTweet, setDidLookupTweet] = useState(false);
    const [tweet, setTweet] = useState();
    const [loading, setLoading] = useState(true);

    const tweetCallback = (response, status) => {
        if (status === 200) {
            setTweet(response);
        }
    }

    useEffect(() => {
        var path = window.location.pathname
        var idRegex = /(?<tweetid>\d+)/
        var match = path.match(idRegex)
        let tweetId;
        if (match) {
            tweetId = match.groups.tweetid
        }
        
        let mounted = true;
        const fetchTweet = async () => {
            if (!mounted || didLookupTweet) {return}
            apiTweetDetail(tweetId, tweetCallback)
            setDidLookupTweet(true);
        }
        fetchTweet();   
        return () => {
            mounted = false;
        }
    }, [tweet, didLookupTweet]);


    return loading ? (
        <div>
            <Tweet tweet={tweet} currentUser={currUser}/>
        </div>
    ) : <p>Loading...</p>;
  
  
}

export default TweetDetail;
