import React, {useState, useEffect} from 'react'
import {UserImages} from './components'
import {apiGetProfileData} from '../api/profiles'
import numeral from 'numeral'

import banner from '../media/forest_banner.jpg'
import './styles.css'
import ProfileEdit from '../components/profile-edit/ProfileEdit'

export const ProfileBadgeComponent = (props) => {
    const [didLookup, setDidLookup] = useState(false);
    const [profileOwner, setProfileOwner] = useState();
    const [isLoading, setIsLoading] = useState(false);
    const { currUser, className, setCurrUser, setProfile } = props

    const getProfileCallback = (response,  status) => {
        console.log(response);
        if (status === 200) {
            setProfileOwner(response);
        } else if (status === 404) {
            alert('User does not exist');
            window.location.href = '/';
        } else if (status === 403 || status === 401) {
            window.location.href = '/login'
        }
    }

    

    useEffect(() => {
        var brokenUrl = window.location.pathname.split('/');
        var username = brokenUrl[brokenUrl.length - 1];
        let mounted = true;
        const fetchProfile = async() => {
            if (!mounted || didLookup) {return}
            apiGetProfileData(getProfileCallback, username, 'GET');
            setDidLookup(true);
            setIsLoading(false);
        }
        fetchProfile();
        return () => {
            mounted = false;
        }
    })

    return (!isLoading && profileOwner) ? <ProfileBadge user={profileOwner} currUser={currUser} setProfile={setProfile} className={className} setCurrUser={setCurrUser} /> : <span>User Not Found</span>;
}


function ProfileBadge(props) {
    const [isEditingProfile, setIsEditingProfile] = useState(false);
    const {user, setProfile, className, setCurrUser, currUser} = props

    const handleFollowToggle = (event) => {
        event.preventDefault();
        event.stopPropagation();
        if (!user) {return}
        apiGetProfileData(handleFollowCallback, user.username, "POST");
    }

    const handleToggleEdit = (event) => {
        event.preventDefault();
        setIsEditingProfile((prevState) => !prevState);
    }

    const handleFollowCallback = (response, status) => {
        if (status === 200) {
            setProfile(response)
        } else if (status === 403) {
            window.location.href = '/login'
        } else {
            alert("We could not find the profile in question. Please try again later.")
        }
    }

    const handleProfileEditCallback = (response, status) => {
        if (status === 200) {
            setCurrUser(response);
        }
        window.location.href = `/profile/${response.username}`
    }


    return user ? (
        <div className={`profile-container ${className}`}>
            <UserImages user={user} banner={banner}/>
            
            <div className="profile__info">
                <h3>{user.username}</h3>
                <p>{`@${user.username}`}</p>

                <div className='bio'>{user.bio}</div>
                <div className='location'>{user.location}</div>

                <div className="follower-following-display">
                   <div className="following-count">
                       <p className="number">{numeral(user.following_count || 0).format("0a")}</p><p className="text">{" "}Following{" "}</p>
                   </div>
                    <div className="follower-count">
                        <p className="number">{numeral(user.follower_count || 0).format("0a")}</p><p className="text">{" "}{user.follower_count === 1 ? 'Follower' : 'Followers'}</p>
                    </div>
                </div>
                
            </div>
            <div className="profile__buttons">
                {user === currUser ? 
                <button onClick={handleToggleEdit} className="profile__action-button">{'Edit Profile'}</button> 
                : <button onClick={handleFollowToggle} className="profile__action-button">{user.is_following ? 'Unfollow' : 'Follow'}</button>}                
                          
            </div>
            {isEditingProfile && <ProfileEdit user={currUser} closeModal={handleToggleEdit} setProfile={setProfile} callback={handleProfileEditCallback}/>}
        </div>
    ) : null;
}



