import './Navbar.css'
import {Link} from 'react-router-dom';
import React, {useEffect, useState} from 'react';


function Navbar(props) {
    const [activeSection, setActiveSection] = useState('Home');
    const {currentUser} = props

    const navbarContent = [
        "Home",
        "Profile",
        "Messages",
        "Settings"
    ]

    function NavbarListItemComponent({content, currentUser, active}) {
        const [path, setPath] = useState('/');

        function listIcon() {
            if (content === 'Home') {
                if (active) {
                    return <i className="fas fa-home bold"></i>
                }
                return <i className="fas fa-home text-thin"></i>
            } else if (content === 'Profile') {
                if (active) {
                    return <i className="fas fa-user-circle"></i>
                }
                return <i class="far fa-user-circle"></i>
            } else if (content === 'Settings') {
                if (active) {
                    return <i className="fas fa-cog bold"></i>
                }
                return <i class="fas fa-cog"></i>
            } else if (content === 'Messages') {
                if (active) {
                    return <i className="fas fa-envelope"></i>
                }
                return <i className="far fa-envelope"></i>
            } 
        }

        const handleLinkClick = (event) => {
            event.preventDefault();
            setActiveSection(content);
            window.location.href = path;
        }

        useEffect(() => {
            if (content === 'Profile') {
                setPath(`/profile/${currentUser ? currentUser.username : ""}`)
            } else if (content === 'Settings') {
                setPath('/settings')
            } else if (content === 'Messages') {
                setPath('/messages')
            }
        }, [content, currentUser]);

    
        return (
            <>
                <Link className="navbar__link" to={path}>
                    <div onClick={handleLinkClick} className={`navbar__list-item ${active && 'bold'}`}>
                        <div className='list-item__icon'>{listIcon()}</div>
                        <div className="list-item__content">{content && content}</div>
                    </div>   
                </Link>     
            </>
        )
    }


    // NAVBAR BELOW
    

    const handleClick = (event) => {
        var hostName = 'http://localhost:3000/'
        event.stopPropagation();
        event.preventDefault();
        let path = window.location.href
        if (path !== hostName) {
            window.location.href = '/'
        }
        
    }

    useEffect(() => {
        let path = window.location.href

        if (path.includes('settings')) {
            setActiveSection('Settings')
        } else if (path.includes('messages')) {
            setActiveSection('Messages')
        } else if (path.includes('profile')) {
            setActiveSection('Profile')
        } else {
            setActiveSection('Home')
        }
    }, []);


    return (
        <div className="navbar__container__outer">
            <div className="navbar__container__inner">
        
                <ul className='navbar__list'>
                    <li className="navbar__header">
                        <i onClick={handleClick} className="fab fa-twitter twitter-icon"></i>
                    </li>
                    {navbarContent.map((content, idx) => <li><NavbarListItemComponent key={idx} content={content} currentUser={currentUser} active={content === activeSection}/></li>)}
                </ul>
            </div>
        </div>
  );
}

export default Navbar;



